* {
  box-sizing: border-box;
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.outter {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  align-items: center;
  width: 700px;
  height: 700px;
  perspective: 1000px;
  /* border: 1px solid red; */
}
.inner {
  width: 30%;
  height: 30%;
  position: relative;
  transform-style: preserve-3d;
  /* border: 1px solid red; */
}
.front,
.back {
  display: flex;
  position: absolute;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  border: 5px solid black;
  font-size: 8em;
  font-weight: bold;
  backface-visibility: hidden;
  /* border-radius: 150px; */
}
.back {
  transform: rotateY(180deg);
}
.front {
  background-color: black;
}
button {
  margin-top: 50px;
  width: max-content;
  padding: 5px 10px;
  font-weight: bold;
  font-size: 5em;
}
.inner.flip {
  transform: rotateY(180deg);
  transition: all 0.5s;
}

